* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
  font-size: 100px;
  color: #fff;
}

.container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  flex-basis: 100%;
  display: flex;
}

.block__1 {
  background-color: rgb(100%, 0%, 0%);
}

.block__2 {
  background-color: rgb(80%, 0%, 0%);
}

.block__3 {
  background-color: rgb(60%, 0%, 0%);
}

.block__4 {
  background-color: rgb(40%, 0%, 0%);
}

.block__5 {
  background-color: rgb(20%, 0%, 0%);
}

.block__6 {
  background-color: rgb(0%, 0%, 0%);
}

.number {
  margin: auto;
}

@media(min-width:600px) {
  .block {
    flex-basis: 50%;
  }

  .block__3 {
    order: 1;
  }

  .block__5 {
    order: 2;
  }

  .block__6 {
    order: 3;
  }

}

@media(min-width:900px) {
  .block {
    flex-basis: 33.33%;
  }
  .block__5 {
    order: 2;
  }

  .block__4 {
    order: 3;
  }
  .block__6 {
    order: 1;
  }

}
